import enUs from "./en-us";
import zhCn from "./zh-cn";

export default {
  computed: {
    i18n() {
      const { locale } = this.$route.params;
      if (locale === "en") {
        return enUs;
      }
      return zhCn;
    },
  },

  methods: {
    isCnLocale() {
      const { locale } = this.$route.params;
      return locale !== "en";
    },
  },
};
